/* Color scheme:

#6B9080
#A4C3B2
#CCE3DE
#EAF4F4
#F6FFF8

*/

html {
  background-color: #EAF4F4
}

/* CSS for a custom scrollbar */

/* width */
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #83af9c; 
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #536e62; 
}

/* CSS for the logo/header */

.title {
  padding-bottom: 0px;
  margin-bottom: 0px;
  margin-left: 0px;
  font-size: 80px;
  font-weight: bold;
  margin-top: 20px;
}

.subheading {
  font-size: 27px;
  margin-bottom: 0px;
  margin-top: 0px;
}

div.heading {
  margin-top: 5px;
  background-color: #6B9080;
  display: flex;
  flex-wrap: nowrap;
}

div.heading img {
  margin-left: 1%;
  margin-right: 25px;
  margin-top: 10px;
  margin-bottom: 10px;
  float: left;
  border-radius: 15px;
}

.headingCol {
  flex: auto;
  border-top: solid 5px #A4C3B2;
  border-bottom: solid 5px #A4C3B2;
  border-right: solid 2.5px #A4C3B2;
  border-left: solid 2.5px #A4C3B2;
  border-radius: 1px;
  transition: background-color 0.3s;
  padding: 10px;
}

.headingCol:hover {
  background-color: #A4C3B2;
}

.headingColimg {
  flex: 0 0 150px;
  border-top: solid 5px #A4C3B2;
  border-bottom: solid 5px #A4C3B2;
  border-right: solid 2.5px #A4C3B2;
  border-left: solid 2.5px #A4C3B2;
  border-radius: 1px;
  transition: background-color 0.3s;
  padding: 10px;
}

.headingColimg:hover {
  background-color: #A4C3B2;
}

.headingText {
  text-align: center;
  margin-right: 30px;
  padding: 50px 0;
  font-size: 22px;
  font-weight: bold;
  transition: color 0.3s;
}

.link {
  text-decoration: none;
  color: black;
  transition: color 0.5s;
  font-weight: bold;
  
}

.headingText .link:hover {
  color: #6B9080;
}

