/* CSS for individual posters */
/* code for hover overlay adapted from https://www.w3schools.com/howto/howto_css_image_overlay.asp*/ 

.movie {
  position: relative;
  border-radius: 15px;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  /* height: 296px;
  width: 200px; */
  opacity: 0;
  transition: .25s ease;
  background-color: #282c34;
  border-radius: 15px;
  width: 200px;
  height: 296px;
}


.movie:hover .overlay {
  opacity: 0.9;
  border-radius: 15px;
}

.movieText {
  color: white;
  font-weight: bold;
  font-size: 17px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  padding-left: 5%;
  padding-right: 5%;
}

