 /* CSS for review textbox */

.WriteReview {
  margin: auto;
  width: 95%;
}
.textarea {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 18px;
  height: 25px;
  width: 95%;
  border: 5px solid #f8f8f8;
  border-radius: 25px;
  background-color: #f8f8f8;
  resize: none;
  padding: 25px;
  overflow: auto;
  transition: border-color 0.3s, height 0.5s ease-in-out;
}

.textarea:focus, input:focus{
  outline: none;
  border-color: #6B9080;
  height: 200px;
}

.WriteReviewContainer {
  background-color: #A4C3B2;
  padding-top: 1px;
  padding-bottom: 1px;
  border-radius: 25px;
  margin: auto;
  margin-top: 25px;
  margin-bottom: 25px;
  width: 95%;
}

.AddReviewButton {
  margin-bottom: 10px;
  margin-top: 10px;
  float: right;
  background-color: #f8f8f8;
  font-size: 18px;
  transition: background-color 0.5s;
  border-radius: 5px;
}

.AddReviewButton:hover {
  cursor: pointer;
  background-color: #6B9080;
}